<template>
  <div class="binary-viewer">
    <!-- <h2>Binary file viewer</h2> -->
    <div class="viewer-container">
      <!-- 파일 선택 영역 -->
      <div class="file-section">
        <input type="file" @change="onFileSelect" ref="fileInput" style="display: none">
        <button @click="fileInput.click()" class="primary-btn">Select File</button>
        <span v-if="fileName" class="file-info">{{ fileName }} ({{ fileSize }})</span>
      </div>

      <!-- 로딩 표시 -->
      <div v-if="isLoading" class="loading-section">
        <div class="progress-bar">
          <div class="progress" :style="{ width: `${progress}%` }"></div>
        </div>
        <span>File loading... {{ progress }}%</span>
      </div>

      <!-- 컨트롤 영역 -->
      <div v-if="fileContent" class="control-section">
        <div class="left-controls">
          <label>
            <input type="checkbox" v-model="showAscii"> Show ASCII
          </label>
          <label>
            <span style="padding-right: 10px;">Bytes/Line</span>
            <select  v-model="bytesPerLine">
              <option :value="8">8</option>
              <option :value="16">16</option>
              <!-- <option :value="32">32</option> -->
            </select>
          </label>
        </div>
        <div class="right-controls">
          <!-- <input 
            type="text" 
            v-model="searchText"
            placeholder="16진수 검색 (예: FF 00)"
            class="search-input"
            @keyup.enter="search"
          > 
          <button @click="search" class="primary-btn">검색</button>-->
        </div>
      </div>

      <!-- 16진수 표시 영역 -->
      <div v-if="fileContent" class="hex-view" ref="hexView">
        <div class="hex-content" :style="{ height: `${totalHeight}px` }">
          <div 
            class="viewport" 
            :style="{ transform: `translateY(${scrollOffset}px)` }"
          >
            <div 
              v-for="line in visibleLines" 
              :key="line.index"
              class="hex-line"
            >
              <!-- 주소 -->
              <div class="address">{{ formatAddress(line.offset) }}</div>
              
              <!-- 16진수 값 -->
              <div class="hex-values">
                <span 
                  v-for="(byte, byteIndex) in line.bytes" 
                  :key="byteIndex"
                  :class="[
                    'hex-byte', 
                    { 
                      highlighted: isHighlighted(line.offset + byteIndex),
                      selected: isSelected(line.offset + byteIndex)
                    }
                  ]"
                  @click="selectByte(line.offset + byteIndex)"
                >{{ formatByte(byte) }}</span>
              </div>
              
              <!-- ASCII 값 -->
              <div v-if="showAscii" class="ascii-values">
                <span 
                  v-for="(byte, byteIndex) in line.bytes" 
                  :key="byteIndex"
                  :class="{ 
                    highlighted: isHighlighted(line.offset + byteIndex),
                    selected: isSelected(line.offset + byteIndex)
                  }"
                  @click="selectByte(line.offset + byteIndex)"
                >{{ toAscii(byte) }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, nextTick, watch } from 'vue'

// 상수
const CHUNK_SIZE = 1024 * 1024 // 1MB
const LINE_HEIGHT = 16 // 20px에서 16px로 줄임
const CACHE_SIZE = 10 * 1024 * 1024 // 10MB 캐시 크기

// refs
const fileInput = ref(null)
const hexView = ref(null)
const fileContent = ref(null)
const fileName = ref('')
const fileSize = ref('')
const isLoading = ref(false)
const progress = ref(0)
const showAscii = ref(true)
const bytesPerLine = ref(16)
//const searchText = ref('')
const highlightedBytes = ref(new Set())
const startLine = ref(0)
const selectedFile = ref(null)
const chunksCache = ref(new Map()) // 청크 캐시
const visibleLinesData = ref([])
const selectedByte = ref(null)

// 계산된 속성
const visibleLines = computed(() => {
  return visibleLinesData.value
})

const totalLines = computed(() => {
  if (!selectedFile.value) return 0
  const fullLines = Math.floor(selectedFile.value.size / bytesPerLine.value)
  const hasRemainder = selectedFile.value.size % bytesPerLine.value > 0
  return fullLines + (hasRemainder ? 1 : 0)
})

const totalHeight = computed(() => {
  return totalLines.value * LINE_HEIGHT
})

const scrollOffset = computed(() => {
  return Math.max(0, startLine.value) * LINE_HEIGHT
})

// 메서드
const formatAddress = (offset) => offset.toString(16).padStart(8, '0').toUpperCase()
const formatByte = (byte) => {
  if (byte === undefined) return '  '
  return byte.toString(16).padStart(2, '0').toUpperCase()
}
const toAscii = (byte) => {
  if (byte === undefined) return ' '
  return byte >= 32 && byte <= 126 ? String.fromCharCode(byte) : '.'
}
const isHighlighted = (index) => highlightedBytes.value.has(index)

const formatFileSize = (bytes) => {
  const units = ['B', 'KB', 'MB', 'GB']
  let size = bytes
  let unitIndex = 0
  
  while (size >= 1024 && unitIndex < units.length - 1) {
    size /= 1024
    unitIndex++
  }
  
  return `${size.toFixed(1)} ${units[unitIndex]}`
}

const loadLinesRange = async (start, end) => {
  const lines = []
  const startOffset = start * bytesPerLine.value
  const endOffset = Math.min(end * bytesPerLine.value, selectedFile.value.size)
  
  // 필요한 청크들을 로드
  await ensureChunksLoaded(startOffset, endOffset)
  
  // 마지막 라인 번호 계산
  const fullLines = Math.floor(selectedFile.value.size / bytesPerLine.value)
  const hasRemainder = selectedFile.value.size % bytesPerLine.value > 0
  const lastLine = fullLines + (hasRemainder ? 0 : -1)
  
  for (let i = start; i <= lastLine && i < end; i++) {
    const offset = i * bytesPerLine.value
    if (offset >= selectedFile.value.size) break
    
    const remainingBytes = selectedFile.value.size - offset
    const lineLength = Math.min(bytesPerLine.value, remainingBytes)
    const bytes = await getBytes(offset, lineLength)
    
    lines.push({
      index: i,
      offset,
      bytes
    })
  }
  
  return lines
}

const getChunkKey = (offset) => {
  return Math.floor(offset / CHUNK_SIZE) * CHUNK_SIZE
}

const ensureChunksLoaded = async (startOffset, endOffset) => {
  const startChunk = getChunkKey(startOffset)
  const endChunk = getChunkKey(endOffset)
  
  for (let offset = startChunk; offset <= endChunk; offset += CHUNK_SIZE) {
    if (!chunksCache.value.has(offset)) {
      await loadChunk(offset)
    }
  }
  
  // 캐시 크기 관리
  while (getCacheSize() > CACHE_SIZE) {
    const oldestKey = chunksCache.value.keys().next().value
    chunksCache.value.delete(oldestKey)
  }
}

const getCacheSize = () => {
  let size = 0
  for (const chunk of chunksCache.value.values()) {
    size += chunk.length
  }
  return size
}

const loadChunk = async (offset) => {
  const chunk = selectedFile.value.slice(
    offset,
    Math.min(offset + CHUNK_SIZE, selectedFile.value.size)
  )
  const buffer = await readChunk(chunk)
  chunksCache.value.set(offset, new Uint8Array(buffer))
}

const getBytes = async (offset, length) => {
  const chunkOffset = getChunkKey(offset)
  const chunk = chunksCache.value.get(chunkOffset)
  if (!chunk) return new Array(length).fill(undefined)
  
  const relativeOffset = offset - chunkOffset
  const bytes = chunk.slice(relativeOffset, relativeOffset + length)
  
  // 파일 끝을 넘어가는 부분은 제거
  if (offset + bytes.length > selectedFile.value.size) {
    bytes.length = selectedFile.value.size - offset
  }
  
  return Array.from(bytes)
}

const onFileSelect = async (event) => {
  const file = event.target.files[0]
  if (!file) return
  
  fileName.value = file.name
  fileSize.value = formatFileSize(file.size)
  selectedFile.value = file
  chunksCache.value.clear()
  startLine.value = 0
  isLoading.value = true
  progress.value = 0
  
  try {
    fileContent.value = true // 먼저 뷰어를 표시
    await nextTick()
    
    // 스크롤 이벤트 핸들러 등록
    if (hexView.value) {
      console.debug('Registering scroll event handler')
      hexView.value.addEventListener('scroll', handleScroll)
    }
    
    // 초기 데이터 로드
    await updateVisibleLines()
  } catch (error) {
    console.error('파일 로딩 오류:', error)
  } finally {
    isLoading.value = false
  }
}

const readChunk = (chunk) => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = e => resolve(e.target.result)
    reader.onerror = reject
    reader.readAsArrayBuffer(chunk)
    //console.debug('readChunk:', reader.length)
  })
}

// const search = () => {
//   const query = searchText.value.trim()
//   if (!query || !fileContent.value) return
  
//   highlightedBytes.value.clear()
  
//   const searchBytes = query
//     .replace(/\s+/g, '')
//     .match(/.{1,2}/g)
//     ?.map(hex => parseInt(hex, 16))
  
//   if (!searchBytes) return
  
//   for (let i = 0; i < fileContent.value.length - searchBytes.length + 1; i++) {
//     if (searchBytes.every((byte, j) => fileContent.value[i + j] === byte)) {
//       for (let j = 0; j < searchBytes.length; j++) {
//         highlightedBytes.value.add(i + j)
//       }
//     }
//   }
// }

const updateVisibleLines = async () => {
  if (!selectedFile.value) return
  
  const containerHeight = hexView.value?.clientHeight || 0
  const visibleCount = Math.ceil(containerHeight / LINE_HEIGHT) + 10 // 버퍼 증가
  const start = Math.max(0, startLine.value - 5)
  const end = Math.min(totalLines.value, startLine.value + visibleCount + 5)
  
  //console.debug('Updating visible lines:', start, '->', end)
  const lines = await loadLinesRange(start, end)
  visibleLinesData.value = lines
}

const handleScroll = () => {
  if (!hexView.value || !selectedFile.value) return
  
  const newStartLine = Math.floor(hexView.value.scrollTop / LINE_HEIGHT)
  if (newStartLine !== startLine.value) {
    startLine.value = newStartLine
    updateVisibleLines()
  }
}

const selectByte = (index) => {
  selectedByte.value = selectedByte.value === index ? null : index
}

const isSelected = (index) => selectedByte.value === index

// bytesPerLine watch 추가
watch(bytesPerLine, (newValue) => {
  document.documentElement.style.setProperty('--bytes-per-line', newValue)
  // 화면 다시 그리기
  updateVisibleLines()
}, { immediate: true })

// 라이프사이클 훅
onMounted(() => {
  console.debug('Component mounted')
})

onUnmounted(() => {
  if (hexView.value) {
    console.debug('Removing scroll event handler')
    hexView.value.removeEventListener('scroll', handleScroll)
  }
})
</script>

